/* Contact Section */
.contact {
  padding: 4rem 2rem;
  background: #f8f9fa;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.submit-btn {
  width: 100%;
  padding: 10px 20px;
  background-color: #007bff; /* Default blue color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem;
}

.submit-btn:hover {
  background-color: #0056b3; /* Darker blue on hover */
  transform: translateY(-2px);
}

/* Button styles when successful */
.submit-btn.success {
  background-color: #28a745; /* Green color on success */
  color: white;
}

/* Button disabled state */
.submit-btn:disabled {
  background-color: #ccc; /* Gray color when disabled */
  cursor: not-allowed;
}

/* Loader animation */
.loader {
  border: 3px solid #f3f3f3; /* Light gray background for the circle */
  border-top: 3px solid #007bff; /* Blue border on top */
  border-radius: 50%; /* Make it circular */
  width: 16px;
  height: 16px;
  animation: spin 0.8s linear infinite; /* Spin animation */
}

/* Keyframes for the spin animation */
@keyframes spin {
  0% {
    transform: rotate(0deg); /* Start position */
  }
  100% {
    transform: rotate(360deg); /* Full rotation */
  }
}
